.background {
  background: linear-gradient(66deg, #071f5a, #312c41, #1a485a, #50024d, #8a1130, #4c0c70);
  background-size: 1200% 1200%;
  -webkit-animation: AnimationName 250s ease infinite;
  -moz-animation: AnimationName 250s ease infinite;
  -o-animation: AnimationName 250s ease infinite;
  animation: AnimationName 250s ease infinite;
}
@-webkit-keyframes AnimationName {
  0% {
    background-position: 0 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}
@-moz-keyframes AnimationName {
  0% {
    background-position: 0 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}
@-o-keyframes AnimationName {
  0% {
    background-position: 0 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}
@keyframes AnimationName {
  0% {
    background-position: 0 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}
